.canvas-container {
    width: calc( 100% - 150px );
    height: calc( 100% - 32px );
    position: fixed;
    top: 32px;
    left: 150px; }

.canvas-background {
    position: fixed;
    z-index: -1; }

body {
    z-index: -2; }

.scroll-container {
    width: calc( 100% - 150px );
    height: calc( 100% - 32px ); }
